import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-menu';

const MenuCurtain = forwardRef(({
  children,
  className,
  show,
  ...remainingProps
}, ref) => (
  <div
    className={classnames(`${CSS_PREFIX}-curtain`, {
      [`${CSS_PREFIX}--show`]: Boolean(show)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </div>
));

MenuCurtain.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Show
   */
  show: bool
};

MenuCurtain.displayName = 'MenuCurtain';

export default MenuCurtain;
